import React from 'react';
import '../styles/Section4.scss';

const Section4 = () => (
    <div className="section section4">
        <div className="background">
            <div className="overlay">
                <div className="content">
                    <div className="subscription">
                        <p className="plan-type"></p>
                        <h2 className="plan-name"></h2>
                    </div>
                    <div className="details">
                        <h1>10000  &nbsp;160+&nbsp;</h1>
                        <p className="description">    -  ,     &nbsp;160&nbsp;</p>
                    </div>
                    <div className="price-details">
                        <p className="price"> 1 </p>
                        <button className="try-button"></button>
                        <p className="trial"> 14   1 </p>
                        <p className="price-details">  97 /.</p>
                        <p className="more-details"></p>
                    </div>

                </div>
            </div>
        </div>
    </div>
);

export default Section4;
